import {useState} from 'react';
import {Skeleton, Alert, Form, Radio, Upload, message, Button, Card} from 'antd';
import {FileAddOutlined, FileDoneOutlined, UploadOutlined} from '@ant-design/icons';

import {Reloader} from './GameLoading';
import {useWishData, WISH_VER} from '../wish';
import {ExtLink} from '../utils';
import {WISH_ROOT, WRITEUP_INSTRUCTION} from '../branding';

import './Writeup.less';

function WriteupForm() {
    let [error, data, load_data] = useWishData('writeup');

    let [uploading, set_uploading] = useState(false);
    let [file, set_file] = useState(null);
    let [publish, set_publish] = useState('Maybe');
    let [rights, set_rights] = useState('CC-BY-NC');

    if(error)
        return <Reloader message={error.error_msg} reload={load_data} />;
    if(data===null)
        return <Skeleton />;

    function do_submit_writeup() {
        if(!file) {
            message.error({content: ' Writeup ', key: 'Writeup.Submit', duration: 3});
            return;
        }

        let form = new FormData();
        form.append('file', file);
        form.append('publish', publish);
        form.append('rights', rights);

        set_uploading(true);

        // hard to believe that in 2022 it is still not fucking possible to track upload progress with `fetch()`
        // see https://stackoverflow.com/questions/35711724/upload-progress-indicators-for-fetch

        const xhr = new XMLHttpRequest();
        xhr.open('PUT', WISH_ROOT+'writeup');
        xhr.setRequestHeader('X-Wish-Version', WISH_VER);
        xhr.withCredentials = true;
        xhr.responseType = 'json';

        xhr.upload.addEventListener('progress', (event)=>{
            if (event.lengthComputable) {
                let percentage = (100 * event.loaded / event.total).toFixed(1);
                message.loading({content: `${percentage}%`, key: 'Writeup.Submit', duration: 20});
            }
        });
        xhr.addEventListener("loadend", () => {
            if(xhr.readyState!==4) {
                message.error({content: ` state=${xhr.readyState}`, key: 'Writeup.Submit', duration: 3});
            } else if(xhr.status!==200) {
                message.error({content: `HTTP  ${xhr.status}`, key: 'Writeup.Submit', duration: 3});
            } else if(xhr.response.error) {
                message.error({content: xhr.response.error_msg, key: 'Writeup.Submit', duration: 3});
            } else {
                message.success({content: '', key: 'Writeup.Submit', duration: 2});
                set_file(null);
                load_data();
            }
            set_uploading(false);
        });

        xhr.send(form);
    }

    return (
        <div>
            {data.writeup_required ?
                <Alert showIcon type="info" message={<>
                     <b></b>  Writeup
                </>} /> :
                <Alert showIcon type="info" message={<>
                     <b></b>  Writeup
                </>} />
            }
            <br />
            {data.submitted_metadata ?
                <Alert showIcon type="success" message=" Writeup" description={<>
                    <ul>
                        {Object.keys(data.submitted_metadata).map((k)=>(
                            <li key={k}>{k}: {data.submitted_metadata[k]}</li>
                        ))}
                    </ul>
                    <p> Writeup </p>
                </>} /> :
                <Alert showIcon type="info" message=" Writeup" />
            }
            <br />
            {WRITEUP_INSTRUCTION}
            <br />
            <Card title=" Writeup" size="small" type="inner" bordered={false}>
                <Form
                    onFinish={do_submit_writeup}
                    colon={false}
                    labelCol={{span: 4}} labelWrap={true} wrapperCol={{span: 20}}
                >
                    <Form.Item label="">
                        <Upload.Dragger
                            fileList={[]}
                            beforeUpload={(f)=>{
                                if(f.size/1024/1024<data.max_size_mb)
                                    set_file(f);
                                else
                                    message.error({content: '', key: 'Writeup.File', duration: 3});
                                return false;
                            }}
                            disabled={uploading}
                        >
                            <p className="ant-upload-drag-icon">
                                {file ? <FileDoneOutlined /> : <FileAddOutlined />}
                            </p>
                            <p className="ant-upload-text">
                                {file ? file.name : ''}
                            </p>
                            <p className="ant-upload-hint">
                                {file ? <>
                                    {(file.size/1024/1024).toFixed(2)}MB
                                </> : <>
                                     .md, .pdf, .pptx, .7z, .zip <br />
                                     {data.max_size_mb}MB Writeup  <b></b> 
                                </>}
                            </p>
                        </Upload.Dragger>
                    </Form.Item>

                    <Form.Item label="" help={
                        publish==='Always-Yes' ?
                            <><b> Writeup </b></> :
                        publish==='Maybe' ?
                            <> Writeup <b> Writeup </b></> :
                        publish==='Always-No' ?
                            <><b> Writeup </b> Writeup</> :
                            <>({publish})</>
                    }>
                        <Radio.Group buttonStyle="solid" value={publish} onChange={(e)=>set_publish(e.target.value)} disabled={uploading}>
                            <Radio.Button value="Always-Yes"></Radio.Button>
                            <Radio.Button value="Maybe"></Radio.Button>
                            <Radio.Button value="Always-No"></Radio.Button>
                        </Radio.Group>
                    </Form.Item>

                    <Form.Item label="" help={
                        rights==='All-Rights-Reserved' ?
                            <> <b></b>  Writeup </> :
                        rights==='CC-BY-NC' ?
                            <>
                                 Writeup  <ExtLink href="https://creativecommons.org/licenses/by-nc/4.0/">CC BY-NC 4.0</ExtLink>
                                {' '} <b></b> 
                            </> :
                        rights==='CC0' ?
                            <>
                                <b> Writeup </b>
                                 <ExtLink href="https://creativecommons.org/publicdomain/zero/1.0/">CC0 1.0</ExtLink>
                                {' '}
                            </> :
                            <>({rights})</>
                    }>
                        <Radio.Group buttonStyle="solid" value={rights} onChange={(e)=>set_rights(e.target.value)} disabled={uploading}>
                            <Radio.Button value="All-Rights-Reserved"></Radio.Button>
                            <Radio.Button value="CC-BY-NC"></Radio.Button>
                            <Radio.Button value="CC0"></Radio.Button>
                        </Radio.Group>
                    </Form.Item>

                    <Button block type="primary" size="large" htmlType="submit" disabled={uploading || file===null}>
                        <UploadOutlined /> 
                    </Button>
                </Form>
            </Card>
        </div>
    );
}


export function Writeup() {
    return (
        <div className="slim-container writeup-container">
            <h1> Writeup</h1>
            <WriteupForm />
        </div>
    );
}